<template>
    <div class="home">
       <myHeader></myHeader>
      <div class="container">
        <div class="crmContent">
            <div class="canI">
                <p class="canITip"></p>
                <p class="canITip"></p>
                <el-steps :active="2" finish-status="success" direction="vertical">
                        <el-step title="" description=":'google'">
                        </el-step>
                        <el-step title="" description=":'google'"></el-step>
                </el-steps>
                <div class='inputY'>
                  <label></label>
                  <input type="text" v-model="yan" placeholder=""/>  
                </div>    
                <el-button style="margin-top: 12px;" @click="secondaryLogin_()"></el-button>
            </div>            
        </div>
        <div class="common-footer"></div>
      </div>
    </div>
  </template>
  
  <script >
    import Vue from 'vue'
    import myHeader from '../common/header'
    import {mapState,mapMutations,mapActions} from 'vuex';
    import '!style-loader!css-loader!less-loader!./style.less';
    import {secondaryLogin} from '../../service/api.js'
    export default {
      name: 'index',
      data() {
        return {
            active: 1,
            yan:null
        }
      },
      components: {
        myHeader
      },
      computed: {
        /*catch store.data*/
        ...mapState({
            userInfo: state => state.login.userInfo
        })
      },
      mounted  () {
  
      },
      methods: {
        async secondaryLogin_(){
          let param={
            code:this.yan
          };
          const {code,data,msg} =await secondaryLogin(param);
          if(code == 1000){
            this.$router.push({
                path: "/home"
            });
          }else{
            alert(msg);
          }
        },

      }
    }
  </script>
  


