<template>
    <div class="bookList">

        <div class="head-box">
            <span class="text"></span>
        </div>
        <el-form :inline="true"  class="search-wrap">
            <el-form-item label="" size="mini">
                            <el-input placeholder="" filterable  v-model='queryCondition.key_word' class="input-with-select">
                              <el-select v-model="queryCondition.type" slot="append" placeholder="" >
                                <el-option label="" value="2"></el-option>
                                <el-option label="" value="1"></el-option>
                                <el-option label="" value="3"></el-option>
                              </el-select>
                            </el-input>
            </el-form-item>
            <el-form-item label="" size="mini">
                <el-select v-model="queryCondition.region_name">
                        <el-option label="" value="true"></el-option>
                        <el-option label="" value="false"></el-option>
                        <el-option label="VIP" value="true"></el-option>
                        <el-option label="" value="false"></el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="" size="mini">
                <el-select v-model="queryCondition.status">
                        <el-option label="" value=""></el-option>
                        <el-option label="" value="0"></el-option>
                        <el-option label="" value="1"></el-option>
                        <el-option label="" value="2"></el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="" size="mini">
                <el-select v-model="queryCondition.s_EQ_status">
                    <el-option
                        v-for="item in payTypeOptions"
                        :key="item.value"
                        :label="item.label"
                        :value="item.value">
                  </el-option>
                </el-select>
            </el-form-item>
            <el-form-item size="mini">
                <el-button type="primary" @click="search()"></el-button>
                <el-button type="primary" @click="clear()"></el-button>
            </el-form-item>
        </el-form>

        <el-table :data="tableData" :border="true" stripe style="width: 100%" >
            <el-table-column prop="id" label="" align="center"></el-table-column>
            <el-table-column prop="table_number" label="" align="center"></el-table-column>
            <el-table-column prop="order_date" label="" align="center"></el-table-column>
            <el-table-column prop="end_date" label="" align="center">          
            </el-table-column>
            <el-table-column prop="sale_price" label="" align="center"></el-table-column>
            <el-table-column prop="finally_price" label="" align="center"></el-table-column>
            <el-table-column prop="finsh_tag" label="" align="center">
                 <template slot-scope="scope">
                    {{scope.row.status == 1 ? "":""}}
                 </template>
            </el-table-column>
            <el-table-column label="" align="center">
                    <template slot-scope="scope">
                        <el-button type="text" size="small" @click="goDetails(scope.row.id)"></el-button>
                    </template>
            </el-table-column>
        </el-table>

        <div class="page">
                <el-pagination
                    @size-change="handleSizeChange"
                    @current-change="handleCurrentChange"
                    :current-page="param.page"
                    :page-sizes="[10, 20, 50]"
                    :page-size="param.per_page"
                     layout="total, sizes, prev, pager, next, jumper"
                    :total="param.total">
                </el-pagination>
        </div>

    </div>
  </template>
  
  <script >
    import Vue from 'vue' // 
    import {mapState,mapMutations,mapActions} from 'vuex';
    import api from '../../../../service/orderManage/index.js'
  
    export default {
      name: 'powerRole',
      data() {
        return {
            queryCondition:{
              type:'',//
              key_word:"",//
              region_name:"",//
              status:"",//
              pay:"",//
              token:"",
              page:1,
              per_page:10
            },
            payTypeOptions:[],
            tableData:[
                {
                    order_number:1994
                }
            ],
            param:{
                page:1,
                per_page:10,
                total:0
            }
        }
      },
      computed: {
            /*catch store.data*/
            ...mapState({
                token_: state => state.token_,
            }),
    },
     mounted () {
         this.payType();
        this.initTable();
      },
      methods: {
        
        /**
         * 
         * @param 1 {arr} XXX
         * @param 2 {obj} XXX
         * @returns  {boolean}
         */
        async initTable(){
            this.queryCondition.token = this.token_;
            const {data,code} = await api.getOrderList(this.queryCondition);
            this.tableData = data.data;
        },

        async payType(){
            const {data,code} = await api.getPayType();
            this.payTypeOptions = data.map((item)=>{
                let obj = {
                    value:item.id,
                    label:item.pay_name
                }
                return obj;
            });
        },


        // 
        search(){
            this.initTable();
        },

        // 
        clear(){
            this.queryCondition = {
              type:'',//
              key_word:"",//
              region_name:"",//
              status:"",//
              pay:"",//
              page:1,
              per_page:10
            };
            this.initTable();
        },

        /**
         * 
         */
        handleSizeChange(num){
            this.queryCondition.page = num;
            this.initTable();
        },
        handleCurrentChange(num){
            this.queryCondition.per_page = num;
            this.initTable();
        },
        goDetails(id){
              this.$router.push({
                name: "orderDetails",
                query: { id: id }
              });
        },

      }
    }
  </script>
<style lang = "less" scoped>
    /* @import url("//unpkg.com/element-ui@2.4.11/lib/theme-chalk/index.css"); */
    @import '../../../../globalCss/var.less';
    @import "style.less";
</style>


