   import {get,post} from './axiosConfig'
   import reHttp from './http'


   /********************  ********************/
   /**
    * 
    */
   export function loginHandle (param) {
       return post(`${reHttp}/api/user/login`,param)
   }

   /**
    * 
    */
   export function secondaryLogin (param) {
        return post(`${reHttp}/api/user/2fa/secondaryLogin`,param)
    }

    /**
    * 
    */
   export function generatorTotpSecret () {
        return post(`${reHttp}/api/user/2fa/generatorTotpSecret`)
    }


    /**
    * 
    */
     export function getUserPermission (param) {
        return post(`${reHttp}/api/permission/getUserPermission`,param)
    }

   /**
    * 
    */
   export function signLoginHandle (timestamp, mobile, signature) {
       //return get('/api/personal/login?timestamp='+timestamp+'&mobile='+mobile+'&signature='+signature)
       return get(`${reHttp}/personal/login?timestamp=${timestamp}&mobile=${mobile}&signature=${signature}`)
   }

   /********************  ********************/
   /**
    * 
    */
   export function updatePassword (submitData) {
       return post(`${reHttp}/api/basics/update_password`, submitData)
   }

    export default {
        loginHandle,
        updatePassword,
        signLoginHandle
    }


