export function post(url, params) {
    return new Promise((resolve, reject) => {
        axios.post(url, params)
            .then(response => {
                resolve(response.data);
            }, err => {
                reject(err);
            })
            .catch((error) => {
               reject(error)
            })
    })
}

export function get(url, params) {
    let symbol = '?';
    if(url.indexOf('?') != -1){
        symbol = '&'
    }
    

    // if(store.state.login.userInfo[0].token){
    //     url = `${url}&token=${store.state.login.userInfo[0].token}`;
    // }

    return new Promise((resolve, reject) => {
        axios.get(url, params)
            .then(response => {
                resolve(response.data);
            }, err => {
                reject(err);
            })
            .catch((error) => {
               reject(error)
            })
    })
}

export default {
    get,
    post
}



