<template>
    <div class="orderDetails">
        <el-row class="row">
            <el-col class="form_item" :span="8">
                {{detailsData.bill_info.bill_code}}
            </el-col>
            <el-col class="form_item" :span="8">
                {{detailsData.bill_info.region_name}}
            </el-col>
            <el-col class="form_item" :span="8">
                {{detailsData.bill_info.sale_name}}
            </el-col>
            <el-col class="form_item" :span="8">
                {{detailsData.bill_info.guest_name}}
            </el-col>
            <el-col class="form_item" :span="8">
                {{detailsData.bill_info.guest_tel}}
            </el-col>
        </el-row>
        
        <el-row class="row" style="border:0;">
            <el-col class="form_item" :span="8">
                <h3></h3>
            </el-col>
            <el-col class="form_item" :span="8" :offset="8">
                
                <span v-if="detailsData.bill_info.finsh_tag =='0'"> </span>
                <span v-if="detailsData.bill_info.finsh_tag =='1'"> </span>
                <span v-if="detailsData.bill_info.finsh_tag =='2'"> </span>
            </el-col>
        </el-row> 
        <el-row class="row" style="border:0;">
            <el-col class="form_item" :span="8">
                 {{detailsData.pay_info.bill_amount}}
            </el-col>
            <el-col class="form_item" :span="8" >
                 {{detailsData.pay_info.should_receive_amount}}
            </el-col>
            <el-col class="form_item" :span="8" >
                &nbsp;   
            </el-col>
            <el-col class="form_item" :span="8" >
                 {{detailsData.pay_info.vip_sale}}
                <!-- <el-badge :value="2" class="item" >
                    &nbsp;
                </el-badge> -->
            </el-col>
            <el-col class="form_item" :span="8" style="color:#F56C6C">
                 {{detailsData.pay_info.had_receive_amount}}
            </el-col>
            <el-col class="form_item" :span="8" >
                &nbsp;   
            </el-col>
            <el-col class="form_item" :span="8" >
                 {{detailsData.pay_info.dish_sale}}
            </el-col>
            <el-col class="form_item" :span="8" >
                &nbsp;   
            </el-col>
            <el-col class="form_item" :span="8" >
                &nbsp;   
            </el-col>
            <el-col class="form_item" :span="8">
                 {{detailsData.pay_info.order_sale}}
            </el-col>
            <el-col class="form_item" :span="8" >
                &nbsp;   
            </el-col>
            <el-col class="form_item" :span="16" style="margin-top:15px">
                <el-table
                    :data="detailsData.payment"
                    border
                    style="width: 100%">
                    <el-table-column
                      prop="pay_name"
                      label=""
                      align="center"
                     >
                    </el-table-column>
                    <el-table-column
                      prop="pay_amount"
                      label=""
                      align="center"
                     >
                    </el-table-column>
                    <el-table-column
                      prop="create_date"
                      label=""
                      align="center"
                     >
                    </el-table-column>
                    <el-table-column
                      prop="cashier_name"
                      label=""
                      align="center">
                    </el-table-column>
                </el-table>
            </el-col>
        </el-row>
        <h3></h3>
        <el-col class="form_item" :span="24" style="border-bottom: 1px solid #d7dde4; padding-bottom:30px;margin-bottom:15px;">
            <el-table
            :data="detailsData.dishes"
             :span-method="objectSpanMethod"
            border
            center
            style="width: 100%;margin-top:15px;">
                <el-table-column
                    align="center"
                    label=""
                >
                </el-table-column>
                <el-table-column
                    align="center"
                    prop="dish_name"
                    label=""
                >
                </el-table-column>
                <el-table-column
                    align="center"
                    prop="dish_qty"
                    label=""
                >
                </el-table-column>
                <el-table-column
                  prop="specification_name"
                  label=""
                  align="center"
                >
                </el-table-column>
                <el-table-column
                    prop="sale_price"
                    label=""
                    align="center">
                </el-table-column>
                <el-table-column
                    label=""
                    align="center">
                     <template slot-scope="scope">
                       {{scope.row.pay_num * scope.row.sale_price}}
                    </template>
                </el-table-column>
                <el-table-column
                    prop="cashier_man"
                    label=""
                    align="center">
                </el-table-column>
                <el-table-column
                    prop="order_date"
                    label=""
                    align="center">
                </el-table-column>
                <el-table-column
                    label=""
                    align="center">
                    <template slot-scope="scope">
                        {{scope.row.pay_num}} 
                    </template>
                </el-table-column>
                <el-table-column
                    prop="order_source"
                    label=""
                    align="center">
                </el-table-column>
            </el-table>  
        </el-col>
        <div v-for="item in detailsData.reverse_reason">
        <el-row>
            <el-col class="form_item" :span="12">
                <h3></h3>
            </el-col>
            <el-col class="form_item" :span="4">
                {{item.pay_num}}
            </el-col>
            <el-col class="form_item" :span="4">
                {{item.cashier}}
            </el-col>
            <el-col class="form_item" :span="4">
                {{item.date}}
            </el-col>
        </el-row>
        <el-row>
            <el-col class="form_item row2" :span="24">
                {{item.name}}
            </el-col>
        </el-row>
        </div>
        <div v-for="item in detailsData.revoke_reason">
        <el-col class="form_item" :span="16">
            <h3></h3>
        </el-col>
        <el-col class="form_item" :span="4">
            {{item.cashier}}
        </el-col>
        <el-col class="form_item" :span="4">
            {{item.date}}
        </el-col>
         <el-col class="form_item row2" :span="24">
            {{item.name}}
        </el-col>
        </div>
        <div v-for="item in detailsData.back_reason">
        <el-col class="form_item" :span="16">
            <h3></h3>
        </el-col>
        <el-col class="form_item" :span="4">
            {{item.cashier}}
        </el-col>
        <el-col class="form_item" :span="4">
            {{item.date}}
        </el-col>
         <el-col class="form_item row2" :span="24">
            {{item.name}}
        </el-col>
        </div>
                
    </div>
  </template>
  
  <script >
    import Vue from 'vue' // 
    import api from '../../../../service/orderManage/index.js'
    import {mapState,mapMutations,mapActions} from 'vuex';
    
    export default {
      name: 'powerRole',
      data() {
        return {
           detailsData:null,
            spanArr:[],
            pos:0,
        }
      },
      components: {
      },
      computed: {
          /*catch store.data*/
            ...mapState({
                token: state => state.token_,
            }),
      },
    mounted () {
        this.getOrderDetail_();
    },
      methods: {

         /**
         * 
         * @param 1 {arr} XXX
         * @param 2 {obj} XXX
         * @returns  {boolean}
         */
        async getOrderDetail_(){
            let param={
                id:this.$route.query.id,
                token:this.token
            }
            let {data,code,error} = await api.getOrderDetail(param);
            if(code == 200){
                this.detailsData = data;
                this.getSpanArr(this.detailsData.dishes);
                window.orderDetailsData = this.detailsData ;
            }else {
                this.$utils.openMessgeTip(error.message, '')
            }
        },

        //table
        objectSpanMethod({ row, column, rowIndex, columnIndex }) {
          if (columnIndex === 0) {
            const _row = this.spanArr[rowIndex];
            const _col = _row > 0 ? 1 : 0;
            return {
              rowspan: _row,
              colspan: _col
            };
          }
        },
    
        //tablerow
        getSpanArr(data) {
          for (var i = 0; i < data.length; i++) {
            if (i === 0) {
              this.spanArr.push(1);
              this.pos = 0;
            } else {
              // 
              if (data[i].sale_category === data[i - 1].sale_category) {
                this.spanArr[this.pos] += 1;
                this.spanArr.push(0);
              } else {
                this.spanArr.push(1);
                this.pos = i;
              }
            }
          }
        },

      }
    }
  </script>
<style lang = "less" scoped>
    @import '../../../../globalCss/var.less';
    @import "style.less";
</style>


